import { RANK } from '../constant'

// action
/* action.data{
  order:false,
  rank: 'juniorcategory/secondarycategory/account/member/', // 
  category:'date/payCount/incomeCount'  //
} */

// 
Array.prototype.mySort = function (cate, order) {
  let list = this
  if (cate.indexOf('pay') >= 0 || cate.indexOf('income') >= 0) {
    cate = 'count'
  }
  list.sort((a, b) => {
    if (order) {
      return a[cate] - b[cate]
    } else {
      return b[cate] - a[cate]
    }
  })
}
// 
const rankSort = (list, n, data) => {  // n1juniorcategory2secondarycategory     dataorder
  list = list.reduce((pre, cur) => {  // 
    let x = cur.category.split('>')[n - 1]
    if (pre[x]) {
      pre[x].push(cur)
    } else {
      pre[x] = [cur]
    }
    return pre
  }, {})
  let arr = []
  for (let key in list) {  // 
    let a = {
      name: key,
      obj: list[key],
      sum: list[key].reduce((pre, cur) => {
        return pre + cur.count * 1
      }, 0)
    }
    arr.push(a)
  }
  arr.mySort('sum', data.demand.order)
  return arr
}
// account member
const amrankSort = (list, data) => {
  list = list.reduce((pre, cur) => {  // 
    if (pre[cur[data.demand.rank]]) {
      pre[cur[data.demand.rank]].push(cur)
    } else {
      pre[cur[data.demand.rank]] = [cur]
    }
    return pre
  }, {})
  let arr = []
  for (let key in list) {  // 
    let a = {
      name: key,
      obj: list[key],
      sum: list[key].reduce((pre, cur) => {
        return pre + cur.count * 1
      }, 0)
    }
    arr.push(a)
  }
  arr.mySort('sum', data.demand.order)
  return arr
}

export default function rank (preState = [], action) {
  const { type, data } = action
  if (type === RANK) {
    let { list } = data
    if (data.list === 'all') {  // listall
      list = JSON.parse(localStorage.getItem('allCount')) || []
    }
    let cat = data.demand.category
    switch (cat) {
      case 'date':  // 
        list.mySort(cat, data.demand.order)
        break
      case 'payCount':  // 
        list = list.filter(item => {   // 
          return item.pay
        })
        list.mySort(cat, data.demand.order)  // order
        break
      case 'incomeCount':  // 
        list = list.filter(item => {    // 
          return !item.pay
        })
        list.mySort(cat, data.demand.order)
        break
    }
    switch (data.demand.rank) {
      case 'juniorcategory':  // 
        list = rankSort(list, 1, data)
        break
      case 'secondarycategory':  // 
        list = rankSort(list, 2, data)
        break
      default:
        list = amrankSort(list, data)
        break
    }
    return [...list]
  }
  return preState
}