import React, { useEffect, useState, useRef } from 'react'
import globalContext from '../../globalContext'
import { Redirect, Switch, Link, Route, useHistory } from 'react-router-dom';
import Head from '../Head/Head';
import IndexPage from '../IndexPage/IndexPage';
import List from '../List/List';
import AddACount from '../AddACount/AddACount';
import style from './style.module.css'
import { Layout, Menu, Button } from 'antd';
import { connect } from 'react-redux';
const { Sider, Content } = Layout;
const Provider = globalContext.Provider;
let mainAnimation = {}
function Index () {
  const mainRef = useRef(null)
  // main
  const mainOut = () => {
    // mainRef.current.className += ` ${style.mainMoveOut}`
  }
  // main 
  const mainIn = () => {
    // mainRef.current.className += ` ${style.mainMoveIn}`
    // setTimeout(() => {
    //   mainRef.current.className = style.content
    // }, 100)
  }
  // mainProvider
  mainAnimation = {
    mainOut,
    mainIn
  }
  // history
  const [items, setData] = useState('/')
  const history = useHistory()
  // location
  useEffect(() => {
    // 
    history.listen(historyLocation => {
      setData(historyLocation.pathname)
    })
  }, [history])

  const addACount = () => {
    history.push('/addACount')
  }

  return (
    <Provider value={mainAnimation}>
      <div className={style.container}>
        <Layout>
          {/*  */}
          <Sider className={style.side} style={{ position: 'fixed', height: '100%' }}>
            <Head style={{ position: 'fixed' }}></Head>
            <Menu
              defaultSelectedKeys={items}
              mode="inline"
              className={style.font}
            >
              <Menu.Item key="/" className={style.font}><Link to='/home'></Link></Menu.Item>
              <Menu.Item key="/list" className={style.font}><Link to='/list'></Link></Menu.Item>
              <Menu.Item key="11" className={style.font}></Menu.Item>
            </Menu>
            <Button type='danger' className={style.addButoon} onClick={addACount}></Button>
          </Sider>

          {/*  */}
          <Content className={style.content} ref={mainRef}>
            {/*  */}
            <Switch>
              <Route path="/home" component={IndexPage} />
              <Route path="/list" component={List} />
              <Route path="/addACount" component={AddACount} />
              <Redirect to='/home' />
            </Switch>
          </Content>
        </Layout>
      </div >
    </Provider>
  )
}

export default connect()(Index)