import { GETCOUNTDATA } from '../constant'

const initState = [{
  count: 0,
  category: [''],
  date: 0,
  account: '',
  pay: true
}]
let allCount = JSON.parse(localStorage.getItem('allCount')) || [initState] // 

// 
const on_demand = (list, demand) => {
  let keys = Object.keys(demand)  // ''
  if (keys.length === 0) {// 
    return list
  } else {
    return keys.reduce((pre, cur) => { // reduce
      return pre.filter(item => { // filterdemand[cur]
        switch (typeof demand[cur]) { // demand[cur]
          case 'object': //  
            if (typeof demand[cur][0] === 'number') { // count date 
              if (demand[cur][0] <= item[cur] && item[cur] <= demand[cur][1]) {
                return item
              }
            } else { // category 
              if (demand[cur][1]) {
                return item[cur].indexOf(demand[cur][1]) !== -1
              } else {
                return item[cur].indexOf(demand[cur][0]) !== -1
              }
            }
            break;
          default: //  pay id member account 
            return item[cur] == demand[cur]
        }
      })
    }, list)
  }
}

// statelistlist
const distList = (item) => {
  let condition = {}
  let nowMsec = new Date().getTime()
  let timeStr = (msecNum) => {
    return {
      year: new Date(msecNum).getFullYear(),
      month: new Date(msecNum).getMonth(),
      day: new Date(msecNum).getDate()
    }
  }
  // [time-segment,time],segment
  let targetMsec = (time, segment) => {
    let segTime = timeStr(time)
    // 00:00:00
    segTime = new Date(segTime.year, segTime.month, segTime.day)
    return [segTime - (segment - 1) * 86400000, time]
  }
  if (typeof item === 'object') {
    condition.date = item
    return on_demand(allCount, condition)
  }
  switch (item) {
    case 'all':
      return allCount
    case 'today':
      condition.date = targetMsec(nowMsec, 1)
      return on_demand(allCount, condition)
    case 'week':
      condition.date = targetMsec(nowMsec, 7)
      return on_demand(allCount, condition)
  }
}
// action.state
/* state_example={
  list:'all',
  demand:{
    
    
    
  }
} */
export default function countDate (preState = initState, action) {
  allCount = JSON.parse(localStorage.getItem('allCount')) || [initState]
  const { type, data } = action
  switch (type) {
    case GETCOUNTDATA:
      return [...on_demand(distList(data.list), data.demand)]
  }
  return preState
}