import { NUMFOR } from '../constant'
// 
// c
// d,'.',
// t
const initState = {
  todayArr: [],
  // 
  todayArr: [],
  // 
  weekArr: [],
  // 
  monthArr: [],
  // 
  yearArr: [],
  // 
  cashArr: [],
  // 
  creditArr: [],
  // 
  bushCardArr: [],
  // 
  aliPayArr: [],
  // 
  weChatArr: [],
  // 
  todayRevenue: 0,
  // 
  weekRevenue: 0,
  // 
  monthRevenue: 0,
  // 
  yearRevenue: 0,
  // 
  cashRevenue: { pay: "0.00", income: "0.00" },
  // 
  creditRevenue: { pay: "0.00", income: "0.00" },
  // 
  bushCardRevenue: { pay: "0.00", income: "0.00" },
  // 
  aliPayRevenue: { pay: "0.00", income: "0.00" },
  // 
  weChatRevenue: { pay: "0.00", income: "0.00" },
  // 
  cashAsset: 0,
  // 
  creditAsset: 0,
  // 
  bushCardAsset: 0,
  // 
  aliPayAsset: 0,
  // 
  weChatAsset: 0,
  // 
  sumAsset: 0,
  // 
  virtualAsset: 0,
}
Number.prototype.numberFormat = function (c, d, t) {
  var n = this,
    c = isNaN(c = Math.abs(c)) ? 2 : c,
    d = d == undefined ? "." : d,
    t = t == undefined ? "," : t,
    s = n < 0 ? "-" : "",
    i = String(parseInt(n = Math.abs(Number(n) || 0).toFixed(c))),
    j = (j = i.length) > 3 ? j % 3 : 0;
  return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
};

export default function numberFormat (pre = initState, action) {
  const { type, data } = action
  if (type === NUMFOR) {
    const deepClone = (obj, flag = false) => {  // 
      if (typeof obj !== 'object' || obj === null) {
        if (flag) {
          let number = obj * 1
          return number.numberFormat(2)
        } else {
          return obj
        }
      }
      if (Array.isArray(obj)) {
        var clone = []
      } else {
        var clone = {}
      }
      for (let key in obj) {
        if (key.indexOf('Asset') >= 0 || key === 'pay' || key === 'income' || key === 'count') {
          clone[key] = deepClone(obj[key], true)
        } else {
          clone[key] = deepClone(obj[key], false)
        }
      }
      return clone
    }
    return { ...pre, ...deepClone(data, false) }
  }
  return pre
}
