import React, { useEffect, useState, useRef, useContext } from 'react'
import { connect } from 'react-redux'
import style from './style.module.css'
import Cascader from '../Cascader/Cascader';
import Calculator from '../../Calculator';
import { Collapse, Calendar, Button, Drawer, notification } from 'antd';
import globalContext from '../../../globalContext';
//  redux  action
import { saveCount, saveAsTemp, editCount } from '../../../redux/actions/saveCount';

const { Panel } = Collapse;

// 
let payCat = {
  '': ['', '', ''],
  '': ['', '', ''],
  '': ['', '', '', '', ''],
  '': ['', '', ''],
  '': ['', '', '', ''],
  '': ['', '', '', '', ''],
  '': ['', '', ''],
  '': ['', '', '', ''],
  '': ['', '', '', ''],
  '': ['', '', '', '', '', ''],
  '': ['', '', '']
}
// 
let incomeCat = {
  '': ['', '', '', '', '', ''],
  '': ['', '', '', '']
}
// 
let AccountCat = {
  '': [''],
  '': ['', '', '', '']
}
// 
let numberCat = {
  '()': ['()'],
  '': ['', '', '', '', '', ''],
  '': ['', '', '', '', '']
}
// 
let initialState = {
  category: '',
  account: '',
  date: new Date().getTime(),
  member: '()',
  tempVisible: false,
  calcuVisible: false,
  count: 0
}

function Index (props) {
  let Consumer = useContext(globalContext)
  let [state, setState] = useState(initialState)
  // 
  let info = {
    id: 0,
    count: 0,
    category: '',
    account: '',
    date: '',
    member: '',
    remark: ''
  }
  useEffect(() => {
    if (props.count && props.count[0].id) {
      setState({ ...props.count[0] })
    }
  }, [props])
  // 
  const receivePayCat = (data) => {
    info.category = `${data[0]} > ${data[1]}`
    setState({ ...state, category: info.category })
  }
  // 
  const receiveAccountCat = (data) => {
    info.account = `${data[1]}`
    setState({ ...state, account: info.account })
  }
  // 
  const receiveNumberCat = (data) => {
    info.member = `${data[1]}`
    setState({ ...state, member: info.member })
  }
  // 
  const onPanelChange = (data) => {
    info.date = data._d
    setState({ ...state, date: data._d })
  }
  // 
  const classification = () => {
    return (
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <h1 style={{ margin: ' 0px', padding: '0px 50px', color: 'gray' }}></h1>
        <span style={{ fontSize: '20px' }}>{state.category}</span>
      </div>
    )
  }

  // 
  const account = () => {
    return (
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <h1 style={{ margin: ' 0px', padding: '0px 50px', color: 'gray' }}></h1>
        <span style={{ fontSize: '20px' }}>{state.account}</span>
      </div>
    )
  }

  // 
  const date = () => {
    let str = `${new Date(state.date).getFullYear()} ${new Date(state.date).getMonth() + 1} ${new Date(state.date).getDate()}`
    return (
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <h1 style={{ margin: ' 0px', padding: '0px 50px', color: 'gray' }}></h1>
        <span style={{ fontSize: '20px' }}>{str}</span>
      </div>
    )
  }
  // 
  const number = () => {
    return (
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <h1 style={{ margin: ' 0px', padding: '0px 50px', color: 'gray' }}></h1>
        <span style={{ fontSize: '20px' }}>{state.member}</span>
      </div>
    )
  }
  // 
  let inputRef = useRef(null)
  const keyDown = (e) => {
    if (e.target.id === 'input' && e.keyCode === 13) {
      conserve(2)
    }
  }
  // 
  let tempInputRef = useRef(null)
  const tempKeyDown = (e) => {
    if (e.keyCode === 13) { conserveTemp() }
  }
  // 
  const conserveTemp = () => {
    const deepClone = (obj) => {    // 
      if (typeof obj !== 'object' || obj === null) {
        return obj
      }
      if (Array.isArray(obj)) {
        var result = []
      } else {
        var result = {}
      }
      for (let key in obj) {
        result[key] = deepClone(obj[key])
      }
      return result
    }
    conserve(1)
    if (!tempInputRef.current.value) {
      notification['error']({
        message: ''
      });
      return null
    }
    let cloneObj = deepClone(info)  // info
    cloneObj.tempName = tempInputRef.current.value
    props.saveAsTemp(cloneObj)
    onClose()
  }
  // 
  const collapseList = () => {
    return (
      <Collapse accordion>
        <Panel header={classification()} key={'paycat'} showArrow={false} style={{ padding: '12px 00px' }}>
          <Cascader obj={props.pay ? payCat : incomeCat} change={receivePayCat}></Cascader>
        </Panel>
        <Panel header={account()} key={'account'} showArrow={false} style={{ padding: '12px 00px' }}>
          <Cascader obj={AccountCat} change={receiveAccountCat}></Cascader>
        </Panel>
        <Panel header={date()} key={'datecat'} showArrow={false} style={{ padding: '12px 00px' }}>
          <div className={style.site_calendar_demo_card}>
            <Calendar fullscreen={false} onChange={onPanelChange} ></Calendar>
          </div>
        </Panel>
        <Panel header={number()} key={'numbercat'} showArrow={false} style={{ padding: '12px 00px' }}>
          <Cascader obj={numberCat} change={receiveNumberCat}></Cascader>
        </Panel>

      </Collapse>
    )
  }
  // 
  const conserve = (s) => {
    if (!verification()) {
      return null
    }
    info.id = state.id ? state.id : (new Date()).getTime()
    info.count = state.count
    info.category = state.category
    info.account = state.account
    info.date = new Date(state.date).getTime()
    info.member = state.member
    info.remark = inputRef.current.value
    info.pay = props.pay
    if (s.toString() === '[object Object]' || s === 2) {
      if (state.id) {
        props.editCount(info)
      } else {
        props.saveCount(info)
      }
    }
  }
  // 
  const conserveAsTemp = () => {
    setState({ ...state, tempVisible: true })
    Consumer.mainOut()
  }
  // 
  const verification = () => {
    if (state.category === '') {
      notification['error']({
        message: ''
      })
      return false
    } else if (state.account === '') {
      notification['error']({
        message: ''
      })
      return false
    }
    return true
  }
  // 
  const onClose = () => {
    setState({ ...state, tempVisible: false })
    Consumer.mainIn()
  }
  // 
  const title = () => {
    return (
      <div className={style.head}>
        <span style={{ fontSize: '25px', fontWeight: 700, lineHeight: '46px' }}>{state.calcuVisible ? '' : ''}</span>
        <Button type="text" onClick={
          () => {
            if (state.tempVisible) return onClose()
            else return closeCalcu()
          }
        } danger style={{ position: 'absolute', right: '10px', fontSize: '20px', color: 'brown' }}>
          
        </Button>
      </div>
    )
  }
  // 
  const openCalcu = () => {
    setState({ ...state, calcuVisible: true })
    Consumer.mainOut()
  }
  // 
  const closeCalcu = () => {
    setState({ ...state, calcuVisible: false })
    Consumer.mainIn()
  }
  // 
  const outPut = (value) => {
    setState({ ...state, count: value })
  }
  return (
    <div className={style.container}>
      <div style={{ borderBottom: '1px solid rgb(248,248,248)', overflow: 'hidden', backgroundColor: 'rgb(248,248,248)', cursor: 'pointer' }} onClick={openCalcu}>
        <span className={style.count} style={{ color: props.pay ? 'green' : 'red' }}>{state.count}</span>
      </div>
      {/*  */}
      {collapseList()}
      <div style={{ width: '100%', borderBottom: '1px solid rgb(217,217,217)', padding: '1px' }}>
        <input className={style.remark} onKeyDown={keyDown} ref={inputRef} placeholder='...' type="text" id='input' />
      </div>
      <div className={style.bottonContainer}>
        <Button type='danger' className={style.conserveButoon} onClick={conserve}></Button>
        <Button type='danger' className={style.conserveAsTempButoon} style={{ display: state.id ? 'none' : 'block' }} onClick={conserveAsTemp}></Button>
      </div>
      {/*  */}
      <Drawer
        title={title()}
        placement="right"
        closable={false}
        onClose={onClose}
        visible={state.tempVisible}
        width={'700px'}
        headerStyle={{ margin: 0, padding: 0 }}
        className={style.drawerBody}
      >
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <h1 style={{ paddingLeft: '60px', paddingTop: '25px', color: 'gray', whiteSpace: 'nowrap' }}></h1>
          <input className={style.remark} ref={tempInputRef} onKeyDown={tempKeyDown} style={{ textAlign: 'right', marginRight: '20px' }} placeholder='' type="text" />
        </div>
        {collapseList()}
        <Button type='danger' className={style.drawerButoon} onClick={conserveTemp}></Button>
      </Drawer>
      <Drawer
        title={title()}
        placement="right"
        closable={false}
        onClose={closeCalcu}
        visible={state.calcuVisible}
        width={'700px'}
        headerStyle={{ margin: 0, padding: 0 }}
        className={style.drawerBody}
      >
        <Calculator initialNum={state.count} closeCalcu={closeCalcu} outPut={outPut}></Calculator>
      </Drawer>

    </div>
  )
}
export default connect(
  state => ({

  }),
  { saveCount, saveAsTemp, editCount }
)(Index)